import { Link } from "@reach/router";
import React, { useRef } from "react";

const AnimatedHeading = ({
  text = "MetaVerse",
  as = "h1",
  link = null,
  className = "",
  hoverColor = "red",
  duration = 0.5,
  onClick,
  linkClass,
}) => {
  const wrapperRef = useRef(null);
  const HeadingTag = as;

  const animateIn = () => {
    wrapperRef.current.classList.add("is-hovered");
  };

  const animateOut = () => {
    wrapperRef.current.classList.remove("is-hovered");
  };

  const content = (
    <HeadingTag className={className}>
      <span
        ref={wrapperRef}
        role="text"
        tabIndex={0}
        onMouseEnter={animateIn}
        onMouseLeave={animateOut}
        onFocus={animateIn}
        onBlur={animateOut}
        onClick={onClick}
        className="ah-wrapper"
        style={{
          "--hover-color": hoverColor,
          "--duration": `${duration}s`,
        }}
      >
        <span className="sr-only">{text}</span>

        <span className="ah-layer ah-original" aria-hidden="true">
          {text}
        </span>
        <span className="ah-layer ah-hover" aria-hidden="true">
          {text}
        </span>
      </span>
    </HeadingTag>
  );

  return link ? (
    <Link to={link} className={`font-black ${linkClass}`}>
      {content}
    </Link>
  ) : (
    content
  );
};

export default AnimatedHeading;
